<template>
   <div class='message-box' v-if='show' @click.self='hidden'>
        <div class='message-box-content'>
            <p class='message-box-close' @click.self='hidden'>X</p>
            <div class='message-box-content'>
                <p class='message-box-title'></p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            show:false,
        }
    },
    methods:{
        hidden(){
            this.show =  false;
        },
    },
}
</script>
<style lang="less" scoped>
  @import '../../common/css/pulic.less';
    .message-box{
         width:100%;
         height:100%;
         background:rgba(0,0,0,0.5);
         position:fixed;
         top:0;
         left:0;
         z-index:8888;
         display:flex;
         justify-content: center;
         align-items: center;
         .message-box-content{
             width:698px;
             height:auto;
             height:715px;
             border:5px #325d6f solid;
             background:white;
             .border-radius(13px,13px);
             position:relative;
              padding:31px 6px 0px 6px;
                .box-sizing(@border-box);
             .message-box-close{
                 width:78px;
                 height:78px;
                 background:#177dc9;
                .border-radius(50%,50%);
                color:white;
                text-align: center;
                line-height: 78px;
                font-size:38px;
                position:absolute;
                right:-20px;
                top:-37px;
                z-index:9999;
             }
             .message-box-content{
                 width:100%;
                 height:100%;
                 backgrond:red;
             }
         }
         
    }
</style>


