            .prototype_type{
                display:block;
                width:31px;
                height:31px;
                .border-radius(15.5px,15.5px);
                position:absolute;
                top:5px;
                left:5px;
            }
            .distance_type{
                display:block;
                width:31px;
                height:31px;
                .border-radius(15.5px,15.5px);
                 position:absolute;
                 right:-3.5px;
                 bottom:-4px;
            }
            .quility_type{
                display:block; 
                width:31px;
                height:31px;
                position:absolute;
                top:0px;
                right:0px;
            }

         }
         .green_li{
             background:url('../../../assets/img/home/sprinte/quality_green.png') 0 0  no-repeat no-repeat;
             background-size:100% 100%;
         }
        .blue_li{
             background:url('../../../assets/img/home/sprinte/quality_blue.png') 0 0  no-repeat no-repeat;
             background-size:100% 100%;
         }
        .purple_li{
             background:url('../../../assets/img/home/sprinte/quality_purple.png') 0 0  no-repeat no-repeat;
             background-size:100% 100%;
         }
          .yellow_li{
             background:url('../../../assets/img/home/sprinte/quality_yellow.png') 0 0  no-repeat no-repeat;
             background-size:100% 100%;
         }
          .red_li{
             background:url('../../../assets/img/home/sprinte/quality_red.png') 0 0  no-repeat no-repeat;
             background-size:100% 100%;
         }

    }
</style>


