            let sprintDiv = document.getElementsByClassName('combat_map_img');
            let i  = 0;
            let stylesText =''
            for(let el of sprintDiv){
                let keyFrames = '';
                if(i < 6){
                    let divX= (el.getBoundingClientRect()).x;
                    let divY =(el.getBoundingClientRect()).y;
                    for(let x = 6;x<12;x++ ){
                         keyFrames =`
                        .sprint-animation${i}${x}{
                            animation: translate${i}${x} 0.7s linear;
                            z-index:1000;
                        }
                        @keyframes translate${i}${x}{
                            to {transform:translate(${ sprintDiv[x].getBoundingClientRect().x-divX}px,${ sprintDiv[x].getBoundingClientRect().y-divY}px);}
                        }`
                        stylesText +=  keyFrames; 
                    }
                }
                if(i > 5){
                    let divX= (el.getBoundingClientRect()).x;
                    let divY =(el.getBoundingClientRect()).y;
                    for(let x = 0;x<6;x++ ){
