                            to {transform:translate(${ sprintDiv[x].getBoundingClientRect().x-divX}px,${ sprintDiv[x].getBoundingClientRect().y-divY}px);}
                        }`
                        stylesText +=  keyFrames; 
                    }
                    
                }
                i++;
            }
            let bodys = document.getElementsByTagName('style')
            bodys[0].innerHTML = bodys[0].innerHTML + stylesText;
            this.beginAnimation();
        },
