                    // hurtNum:100,//减少得血量值
                    // attack:1//攻击方1，自己，2敌方
            let sprintDiv = document.getElementsByClassName('combat_map_img');
            let animationName ='sprint-animation';
            //div索引位置
            let leftMoveSprintPlace ={
                '1':1,
                '2':3,
                '3':5,
                '4':0,
                '5':2,
                '6':4,
            };
            //div索引位置 敌方位置
            let rightMoveSprintPlace ={
                '1':6,
                '2':8,
                '3':10,
                '4':7,
                '5':9,
                '6':11,
            };
            let hurtDiv = document.getElementsByClassName('combat_map_shownum');
            let liftbarDiv = document.getElementsByClassName('combat_map_liftbar');//血条
            let testFun = (item,index)=>{ 
                //获取攻击方位置转换
                let leftPlace = leftMoveSprintPlace[item.attackEl];
                let rightPlace = rightMoveSprintPlace[item.attackEl];
                //被攻击方位置转换
                let leftPlaceReceive = leftMoveSprintPlace[item.receiveEl];
                let rightPlaceReceive = rightMoveSprintPlace[item.receiveEl];
                //获取血量伤害div
                return new Promise(async (resolve,reject)=>{
                    //攻击方
                    let that = this;
                    if(item.attack === 1){
                        //console.log('远近',this.selfChoseSprint[item.attackEl].DistanceType === 1)
                        if(this.selfChoseSprint[item.attackEl].DistanceType === 1){
                            let animationFun=function(){
                                    sprintDiv[leftPlace].classList.remove(animationName+leftPlace+rightPlaceReceive);
                                    sprintDiv[leftPlace].removeEventListener('animationend',animationFun,false);
                                    //处理被伤害值
                                    hurtDiv[rightPlaceReceive].innerHTML=item.hurtNum;
                                    hurtDiv[rightPlaceReceive].classList.add('sprint_animation_text');
                                    //处理血条
                                    // console.log(item.attackEl,that.rivalChoseSprint[item.attackEl])
                                    that.rivalChoseSprint[item.receiveEl].hp -= item.hurtNum;
                                   // that.rivalChoseSprint[item.receiveEl].hp=that.rivalChoseSprint[item.receiveEl].hp<0?0:that.rivalChoseSprint[item.receiveEl].hp;
                                    liftbarDiv[rightPlaceReceive].style.width =that.rivalChoseSprint[item.receiveEl].hp<0?'0px':(that.rivalChoseSprint[item.receiveEl].hp/that.rivalChoseSprint[item.receiveEl].hpSave )*100+'%';
                                    console.log('npstop',that.rivalChoseSprint[item.receiveEl].hp)
                                    let textAnimationFun =()=>{
                                        //处理血条
                                         console.log('xuetiao',that.rivalChoseSprint[item.receiveEl].hp)
                                        hurtDiv[rightPlaceReceive].classList.remove('sprint_animation_text');
                                        hurtDiv[rightPlaceReceive].removeEventListener('animationend',textAnimationFun,false);
                                        resolve(false)
                                    }
                                    that.rivalChoseSprint[item.receiveEl].hp<0?resolve(false):'';
                                    hurtDiv[rightPlaceReceive].addEventListener('animationend',textAnimationFun,false)
                                    
                                };
                            sprintDiv[leftPlace].classList.add(animationName+leftPlace+rightPlaceReceive);
                            await sprintDiv[leftPlace].addEventListener('animationend',animationFun,false);
                            return false;
                        }else{
                            let animationFun=function(){
                                    sprintDiv[leftPlace].classList.remove('sprint_animation_move');
                                    sprintDiv[leftPlace].removeEventListener('animationend',animationFun,false);
                                    //被伤害值
                                    hurtDiv[rightPlaceReceive].innerHTML=item.hurtNum;
                                    hurtDiv[rightPlaceReceive].classList.add('sprint_animation_text');
                                    //处理血条
                                    that.rivalChoseSprint[item.receiveEl].hp -= item.hurtNum;
                                    liftbarDiv[rightPlaceReceive].style.width =(that.rivalChoseSprint[item.receiveEl].hp/that.rivalChoseSprint[item.receiveEl].hpSave )*100+'%';
                                    let textAnimationFun =()=>{
                                        hurtDiv[rightPlaceReceive].classList.remove('sprint_animation_text');
                                        hurtDiv[rightPlaceReceive].removeEventListener('animationend',textAnimationFun,false);
                                        resolve(false)
                                    }
                                     that.rivalChoseSprint[item.receiveEl].hp<0?resolve(false):'';
                                    hurtDiv[rightPlaceReceive].addEventListener('animationend',textAnimationFun,false)
                                   
                                };
                            sprintDiv[leftPlace].classList.add('sprint_animation_move');
                            await sprintDiv[leftPlace].addEventListener('animationend',animationFun,false);
                            return false;
                        }
                        
                    }
                    if(item.attack === 2){
                        if(this.rivalChoseSprint[item.attackEl].DistanceType === 1){
                            let animationFun=function(){
                                sprintDiv[rightPlace].classList.remove(animationName+rightPlace+leftPlaceReceive);
                                sprintDiv[rightPlace].removeEventListener('animationend',animationFun,false);
                                //伤害值动画展示
                                hurtDiv[leftPlaceReceive].innerHTML=item.hurtNum;
                                hurtDiv[leftPlaceReceive].classList.add('sprint_animation_text');
                                //处理血条
                                that.selfChoseSprint[item.receiveEl].hp -= item.hurtNum;
                                liftbarDiv[leftPlaceReceive].style.width =(that.selfChoseSprint[item.receiveEl].hp/that.selfChoseSprint[item.receiveEl].hpSave )*100+'%';
                                let textAnimationFun =()=>{
                                    hurtDiv[leftPlaceReceive].classList.remove('sprint_animation_text');
                                    hurtDiv[leftPlaceReceive].removeEventListener('animationend',textAnimationFun,false);
                                    resolve(false)
                                }
                                that.selfChoseSprint[item.receiveEl].hp<0?resolve(false):'';
                                hurtDiv[leftPlaceReceive].addEventListener('animationend',textAnimationFun,false)
                            };
                            sprintDiv[rightPlace].classList.add(animationName+rightPlace+leftPlaceReceive)
                            sprintDiv[rightPlace].addEventListener('animationend',animationFun,false)
                            return false;
                        }else{
                            let animationFun=function(){
                                console.log(item.hurtNum)
                                sprintDiv[rightPlace].classList.remove('sprint_animation_move');
                                sprintDiv[rightPlace].removeEventListener('animationend',animationFun,false);
                                 //伤害值动画展示
                                hurtDiv[leftPlaceReceive].innerHTML=item.hurtNum;
                                hurtDiv[leftPlaceReceive].classList.add('sprint_animation_text');
                                //处理血条
                                that.selfChoseSprint[item.attackEl].hp -= item.hurtNum;
                                liftbarDiv[leftPlaceReceive].style.width =(that.selfChoseSprint[item.attackEl].hp/that.selfChoseSprint[item.attackEl].hpSave )*100+'%';
                                let textAnimationFun =()=>{
                                    hurtDiv[leftPlaceReceive].classList.remove('sprint_animation_text');
                                    hurtDiv[leftPlaceReceive].removeEventListener('animationend',textAnimationFun,false);
                                    resolve(false)
                                }
                                that.selfChoseSprint[item.receiveEl].hp<0?resolve(false):'';
                                hurtDiv[leftPlaceReceive].addEventListener('animationend',textAnimationFun,false)
                            };
                            sprintDiv[rightPlace].classList.add('sprint_animation_move');
                            sprintDiv[rightPlace].addEventListener('animationend',animationFun,false);
                            return false
                        }
                    }
                })}
             
          
            for(let i = 0;i<this.combatDatList.length;i++){
                await testFun(this.combatDatList[i],i)
            }
            
        },
        getStyles(){},
        //取消
        cancelFun(){
            this.$router.go(-1);
        },
    },
    mounted(){
       this.$nextTick(()=>{
           this.setAnimation();
           
       })
    },
    created(){
        this.selfChoseSprint = this.$route.query.selfChoseSprint;
        for(let key in this.selfChoseSprint){
            this.selfChoseSprint[key].hpSave = this.selfChoseSprint[key].hp;
        }
        for(let key in this.rivalChoseSprint){
             this.rivalChoseSprint[key].hpSave = this.rivalChoseSprint[key].hp;
        }
       
    },

}
</script>
